import React from "react"
import styled from "styled-components"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faQuoteLeft, faQuoteRight } from "@fortawesome/free-solid-svg-icons"
import vCover from "../../assets/images/banner.jpg"
import vSrc from "../../assets/videos/banner.mp4"
import sWind from "../../assets/songs/wind.mp3"
import sMusic from "../../assets/songs/music.mp3"

const Container = styled.div`
		height: 100vh;
		position: relative;
	`,
	VideoWrap = styled.div`
		height: 100%;
		overflow: hidden;
		position: absolute;
		top: 0;
	`,
	Video = styled.video`
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: 34% center;
	`,
	Tip = styled.p`
		color: rgba(255, 255, 255, 0.3);
		position: absolute;
		right: 5px;
		bottom: 5px;
	`,
	Greet = styled.div`
		height: 100%;
		padding: 0 15px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		transform: translateY(180px);
		position: relative;
		z-index: 1;
	`,
	SayHi = styled.h1`
		margin-bottom: 20px;
		color: #fff;
		font-size: 64px;
		font-weight: bolder;
		text-shadow: 6px 6px 12px rgba(0, 0, 0, 0.7);
		@media (max-width: 468px) {
			font-size: 38px;
		}
	`,
	Intro = styled.p`
		padding: 20px 30px;
		color: #ccc;
		font-size: 16px;
		background: rgba(0, 0, 0, 0.5);
		border-radius: 12px;
		position: relative;
		&::after {
			content: "";
			border-width: 15px;
			border-style: solid;
			border-color: transparent transparent rgba(0, 0, 0, 0.5) transparent;
			position: absolute;
			top: -30px;
			left: 20%;
		}
		@media (max-width: 468px) {
			padding: 15px 20px;
		}
	`,
	QuoteLeft = styled(FontAwesomeIcon).attrs({ icon: faQuoteLeft })`
		margin-right: 5px;
		font-size: 13px;
		transform: translateY(-3px);
	`,
	QuoteRight = styled(FontAwesomeIcon).attrs({ icon: faQuoteRight })`
		margin-left: 5px;
		font-size: 13px;
		transform: translateY(-3px);
	`

export default function Banner() {
	return (
		<Container>
			<VideoWrap>
				<Video poster={vCover} loop muted autoPlay>
					<source src={vSrc} type="audio/mp4" /> 您的浏览器不支持播放此视频.
				</Video>
				<audio loop autoPlay>
					<source src={sMusic} type="audio/mp3" />
				</audio>
				<audio loop autoPlay>
					<source src={sWind} type="audio/mp3" />
				</audio>
				<Tip>音视频资源来自《原神》</Tip>
			</VideoWrap>
			<Greet>
				<SayHi>Hi,My Friends</SayHi>
				<Intro>
					<QuoteLeft />
					Although we are strangers, we are friends when you visit my blog
					<QuoteRight />
				</Intro>
			</Greet>
		</Container>
	)
}
